<template>
	<view class="repair-form-page">
		<view class="repair-form">
			<uni-card margin="2">
				<view class="repair-title">
					<text>施工验收表</text>
				</view>
				<uni-forms ref="repairForm" label-position="left" label-width="75px" :modelValue="repairFormData"
					:rules="repairFormRules">
					<uni-forms-item required label="表名称" name="title">
						<uni-easyinput v-model="repairFormData.title" placeholder="请输入表名称" />
					</uni-forms-item>
					<uni-forms-item required label="姓名" name="name">
						<uni-easyinput v-model="repairFormData.name" placeholder="请输入残疾人姓名" />
					</uni-forms-item>
					<uni-forms-item label="残疾证号码" name="dis_id" required>
						<uni-easyinput v-model="repairFormData.dis_id" placeholder="请输入残疾证号码" />
					</uni-forms-item>
					<uni-forms-item label="联系电话" name="mobile" required>
						<uni-easyinput v-model="repairFormData.mobile" placeholder="请输入联系电话" />
					</uni-forms-item>
					<uni-forms-item label="家庭住址" name="address" required>
						<uni-easyinput type="textarea" v-model="repairFormData.address" placeholder="请输入家庭住址" />
					</uni-forms-item>
					<uni-forms-item label="改造内容" name="detail" required>
						<uni-easyinput type="textarea" v-model="repairFormData.detail" placeholder="请输入申请改造内容" />
					</uni-forms-item>
				</uni-forms>
				<button class="button" @click="repairSubmit('repairForm')">提交</button>
			</uni-card>
		</view>
		<view>
			<uni-popup ref="repairDialog" type="dialog">
				<uni-popup-dialog type="info" cancelText="关闭" confirmText="同意" title="通知" content="是否确认提交"
					@confirm="repairConfirm"></uni-popup-dialog>
			</uni-popup>
		</view>
		<view>
			<!-- 提示信息弹窗 -->
			<uni-popup ref="errMessage" type="message">
				<uni-popup-message type="error" :message="messageText" :duration="500"></uni-popup-message>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'repairOrderForm',
		props: {
			recordid: {
				type: Number,
				required: true
			},
			newrepair: {
				type: String,
				required: true
			}
		},
		created() {
			//传的值不对也有可能是类型不对
			//通过qurey方式传参值都会变成字符串形式
			if (this.newrepair == 'false') {
				this.getWorkdetail()
			}
		},
		data() {
			return {
				record_id: this.recordid,
				massageText: "",
				repairFormData: {
					title: null,
					name: null,
					dis_id: null,
					mobile: null,
					address: null,
					detail: null
				},
				repairFormRules: {
					title: {
						rules: [{
							required: true,
							errorMessage: '表名称不能为空'
						}]
					},
					name: {
						rules: [{
							required: true,
							errorMessage: '姓名不能为空'
						}]
					},
					dis_id: {
						rules: [{
							required: true,
							errorMessage: '残疾证号码不能为空'
						}]
					},
					mobile: {
						rules: [{
							required: true,
							errorMessage: '联系电话不能为空'
						}]
					},
					address: {
						rules: [{
							required: true,
							errorMessage: '家庭住址不能为空'
						}]
					},
					detail: {
						rules: [{
							required: true,
							errorMessage: '改造内容不能为空'
						}]
					}
				},
			};
		},
		methods: {
			repairSubmit(ref) {
				this.$refs[ref].validate().then(res => {
					this.$refs.repairDialog.open()
				}).catch(err => {
					console.log('err', err);
				})
			},
			repairConfirm() {
				const that = this
				const api = this.newrepair ? "/checktable/create/" : "/checktable/modify/"
				this.repairFormData.record_id = this.record_id
				uni.showLoading({
					title: "正在提交中..."
				})
				uni.$http.post(api, this.repairFormData).then((res) => {
					if (res.data.code == 400) {
						this.showToast({
							icon: "error",
							title: res.data.errmsg
						})
					} else if (res.data.code == 0) {
						uni.showToast({
							title: `提交成功`
						})
						setTimeout(() => {
							this.$emit("finishStepone")
						}, 100)
					} else {
						this.showToast({
							icon: "error",
							title: "未知错误"
						})
					}
				}).catch((err) => {
					this.showToast({
						icon: "error",
						title: "系统错误"
					})
				})
			},
			getWorkdetail() {
				uni.showLoading({
					title: "数据加载中"
				})
				uni.$http.get(`/info/${this.record_id}`).then(res => {
					if (res.data.code == 400) {
						uni.showToast({
							icon: 'error',
							title: res.data.errmsg
						})
					} else if (res.data.code == 0) {
						uni.hideLoading()
						const {
							checktable
						} = res.data.data
						//因为vue2中不能实时监听对象的添加，所以要先对对象进行属性赋空

						if (this.newrepair == 'true') {
							this.repairFormData = {
								title: null,
								name: null,
								dis_id: null,
								mobile: null,
								address: null,
								detail: null
							}
						} else {
							this.repairFormData = checktable
						}
					} else {
						uni.showToast({
							icon: 'error',
							title: "未知错误"
						})
					}
				}, err => {
					uni.showToast({
						icon: 'error',
						title: "系统错误"
					})
				})
			},
		}
	}
</script>

<style lang="scss">
	.repair-form-page {
		.repair-form {
			margin-top: 30rpx;

			.repair-title {
				text-align: center;
				font-size: 18px;
				margin-bottom: 20rpx;
			}

			.button {
				background-color: $work-color-main;
				height: 70rpx;
				width: 400rpx;
				line-height: 70rpx;
				color: white;
				font-size: 14px;
				margin-bottom: 70rpx;
			}
		}
	}
</style>
